.todo-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;

  &__item {
    padding: 0 15px;
    border: 4px solid #0066b2;
    border-radius: 1em;
    transition: all 0.5s;

    &:hover {
      box-shadow: 7px 7px #89CFF0;
      transform: translate(0, -10px);
    }
  }
}